      margin-right: 24px; } }
  @media (min-width: 1440px) {
    .profile {
      width: 350px; } }

.profile__main-info {
  position: relative; }
  @media (min-width: 768px) {
    .profile__main-info {
      display: flex;
      justify-content: flex-start; } }

@media (max-width: 767px) {
