  @media (min-width: 768px) {
    .profile__name {
      text-align: left; } }

.profile__username {
  padding-bottom: 16px;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: -0.5px;
  color: #3E3F58;
  text-align: center; }
  @media (min-width: 768px) {
    .profile__username {
      text-align: left; } }

.profile__edit {
  position: relative;
  padding-left: 30px;
  font-size: 16px;
  line-height: 20px;
  color: #F05D63;
  text-decoration: underline; }
  .profile__edit::before {
    content: "";
