.profile__description {
  font-size: 16px;
  line-height: 24px;
  color: #3E3F58; }

.menu__toggle {
  position: absolute;
  width: 16px;
  height: 16px;
  right: 0;
  bottom: 0;
  padding: 0;
  cursor: pointer;
  border: none;
  background-color: transparent; }
  @media (min-width: 768px) {
    .menu__toggle {
      right: 20px;
      top: 0; } }
  .menu__toggle::before {
    content: "";
    position: absolute;
    width: 4px;
    height: 4px;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
