  font-size: 16px;
  line-height: 24px;
  color: #636363; }

@media (max-width: 767px) {
  .content {
    padding-top: 40px; } }

@media (min-width: 768px) {
  .content {
    margin-left: 24px; } }

.slider {
  display: flex;
  justify-content: space-between;
  width: 202px;
  margin: 0 10px; }

.slider__label {
  font-size: 16px;
  font-weight: 600;
