  color: #3E4056;
  cursor: pointer; }
  .slider__label:hover {
    opacity: 60%; }

.slider__scale {
  position: relative;
  width: 40px;
  height: 24px;
  border-radius: 20px;
  transition: all 0.2s ease-out; }
  .slider__scale::before {
    content: "";
    position: absolute;
    display: block;
    width: 18px;
    height: 18px;
    top: 50%;
    transform: translateY(-50%);
    background-color: #fff;
    border-radius: 50%;
    transition: all 0.2s ease-out; }
  .slider__scale--active {
    background-color: #ED5267; }
    .slider__scale--active::before {
      right: 3px; }

.slider__public:checked ~ .slider__scale::before {
  left: 3px; }

.slider__public:checked ~ .slider__scale {
  background-color: #B2B3BB; }

.articles {
